
import { defineComponent } from 'vue';
import Pagination from '@/shared/components/Pagination/Pagination.vue';
import { COM21200111_BODY, COM21200111_RES, IB_ROLE_LIST } from '@/shared/type/COM21200111.type';
import { PAGE_SIZE } from '@/shared/constants/common.const';
import { modalController } from "@ionic/vue";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';

export default defineComponent({
    name: "com212001sc00",
    components: {
        Pagination,
        BreadCrumb,
        SelectOption
    },

    data() {
        return {
            dataTable: [] as IB_ROLE_LIST[],
            searchField: {
                userRoleID: "",
                userRoleStatusCode: "",
            },
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            resetPage: false,
        }
    },

    created() {
        this.inquiryRoleList()
    },

    computed: {
        headers() {
            return [
                { columnName: "No." },
                { columnName: "View" },
                { columnName: "Role ID" },
                { columnName: "Role Name" },
                { columnName: "Role Description" },
                { columnName: "	Status" },
            ]
        }
    },

    methods: {

        async inquiryRoleList(mode?: string) {
            const body: COM21200111_BODY = {
                userRoleID: this.searchField.userRoleID.trim(),
                userRoleStatusCode: this.searchField.userRoleStatusCode,
                pageSize: this.pageSize,
                pageNumber: mode == 'search' ? 0 : this.currentPage,
            }

            if (mode === 'search') {
                this.currentPage = 1
                this.resetPage = true;
            }

            const res = await this.$request.getRequest('COM21200111', body, false) as COM21200111_RES;
            if (res.header.result) {

                this.dataTable = JSON.parse(JSON.stringify(this.convertCodeToValue(res.body.list)))
                this.totalItems = res.body.totalCount;
            } else {
                this.dialogMessage(res.header.error_text)
            }
        },

        convertCodeToValue(data: IB_ROLE_LIST[]): IB_ROLE_LIST[] {
            return data.map((item: IB_ROLE_LIST) => {
                return {
                    userRoleID: item.userRoleID,
                    userRoleName: item.userRoleName,
                    userRoleDescription: item.userRoleDescription,
                    userRoleStatusCode: `${item.userRoleStatusCode} : ${this.$codeConverter.convertValue('BusinessCommonStatusCode', item.userRoleStatusCode)}`
                }
            })
        },

        handleRegister() {
            this.$router.push({
                path: 'COM212001SC01',
                query: {
                    type: 'register'
                }
            })
        },

        handleRowClick(index: number, rowData: IB_ROLE_LIST) {
            this.$router.push({
                path: 'COM212001SC01',
                query: {
                    type: 'detail',
                    userRoleID: rowData.userRoleID
                }
            });
        },

        handleRefresh() {
            this.searchField.userRoleID = "";
            this.searchField.userRoleStatusCode = "";
            this.resetPage = true;
            this.currentPage = 1
            this.inquiryRoleList();
        },

        handlePagination(pageNumber: number) {
            this.resetPage = false;
            this.currentPage = pageNumber
            this.inquiryRoleList()
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        },

        dialogMessage(message: string) {
            this.$dialog.onOpen({
                title: ``,
                content: String(message),
                showCancel: false,
                callback: () => {
                    modalController.dismiss()
                }
            });
        },
    }
})
